.button-container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button-container button {
  color: #333;
  cursor: pointer;
  background-color: #e0e0e0;
  border: none;
  border-radius: 4px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.button-container button:hover, .button-container button:focus {
  color: #fff;
  background-color: #ccc;
}

